@import url(https://fonts.googleapis.com/css?family=Bangers|Cinzel:400,700,900|Lato:100,300,400,700,900|Lobster|Lora:400,700|Mansalva|Muli:200,300,400,600,700,800,900|Open+Sans:300,400,600,700,800|Oswald:200,300,400,500,600,700|Roboto:100,300,400,500,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.ImageSection{
    margin-bottom: 100px;
}
.SocialBar{
    margin-left: auto;
}

@media (min-width: 200px) and (max-width: 600px){
    header .searchTop {
        flex-direction: column;
        padding-top: 15px;
        padding-bottom: 5px;
        padding-left: 20px;
        padding-right: 20px;
        justify-content: center;
    }
    header .searchTop .Search {
        width: 80%;
        margin: 0 auto 0px auto;
    }
    
    header .searchTop .logo-holder {
        text-align: center;
        width: 60%;
        height: 78px;
    }
    header .searchTop .logo-3 h3 {
        color: #e74c3c;
        font-family: 'Oswald', sans-serif;
        font-weight: 300;
        font-size: 30px;
        line-height: 1.3;
    }
    header .searchTop .logo-3 p {
        font-size: 10px;
        letter-spacing: 7px;
        text-transform: uppercase;
        background: #34495e;
        font-weight: 400;
        color: #fff;
        padding-left: 5px;
    }
    header .headercss{
        justify-content: center;
        align-items: center;
    }
    .ImageSection div .introContainer{
        padding-top: 2px;
        padding-right: 0; 
        padding-left: 0; 
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .ImageSection div .introContainer img{
        margin-right: auto;
        height: 120px;
        width: 120px;
    }
    .ImageSection div .introContainer #aboutme h1{
        padding-top: 10px;
    }
    .ImageSection div .introContainer #aboutme p{
        margin-top: -21px;
        margin-bottom: 1rem;
        font-size: small;
        padding: 15px;
    }
    .SocialBar{
        margin-left:none;
        margin:auto;
    }
    
    .ImageSection div .imageCards {
        flex-wrap: nowrap;
        flex-direction: column;
        margin: auto;
        margin-left: 0;
        margin-right: 0;
    }
    .imageCards .card2{
        padding: 10px 10px;
        margin: auto;
        width: 80%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .card2 #blogSlicedContent{
        font-size: 11px;
    }
    .card2 #blogSlicedContent h1{
        font-size:15px;
    }
    .card2 #blogSlicedContent h2{
        font-size:13px;
    }
    .card2 img {
        height: 150px;
        width: 90%;
    }

    /*  */
    .blogPageIntro div .introContainer{
        padding-top: 2px;
        padding-right: 0; 
        padding-left: 0; 
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .blogPageIntro .imageCards {
        flex-wrap: nowrap;
        flex-direction: column;
        margin: auto;
        margin-left: 0;
        margin-right: 0;
    }
    .blogPageIntro div .introContainer img{
        margin-right: auto;
        height: 120px;
        width: 120px;
    }
    .blogPageIntro div .introContainer #aboutme h1{
        padding-top: 10px;
    }
    .blogPageIntro div .introContainer #aboutme p{
        margin-top: -21px;
        margin-bottom: 1rem;
        font-size: small;
        padding: 15px;
    }
    .blogPageIntro .blogPostPage{
        margin-left: 0;
        margin-right: 0;
        padding-left: 10px;
        padding-right: 10px;
    }
    .blogPageIntro .blogPostPage .blogImageContainer img{
        width:90%;
    }
