    border-color: #a3a3a3 #504d4d #b1acac;
}

.introContainer{
    background-color: #f5f5f5e3;;
    display:flex;
    padding-top: 30px;
    overflow: hidden;
    -webkit-animation: slideIn;
            animation: slideIn;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    margin-bottom: 2.5rem;
    padding-right: 150px;
    padding-left: 150px;
}
.introContainer img {
    border-radius: 50%;
    height: 160px;
    width: 160px;
